import React, { useState } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, Typography } from '@mui/material';

export default function ReservationForm({ visible, onClose, selectedSeats, onReserve, selectedDate }) {
  // state
  const [name, setName] = useState('');
  const [department, setDepartment] = useState('');

  // visiblefalse
  if(!visible || selectedSeats.length === 0) return null;

  const handleReserve = () => {
    // ""onReserve
    onReserve({ name, department });
    // 
    setName('');
    setDepartment('');
  };

  return (
    <Dialog open={visible} onClose={onClose} maxWidth="sm" fullWidth
      PaperProps={{
        style: { zIndex:9999 } // 
      }}
    >
      <DialogTitle></DialogTitle>
      <DialogContent>
        <Typography variant="body1" gutterBottom>
          {selectedDate} 
        </Typography>
        <Typography variant="body2" gutterBottom>
          : {selectedSeats.map(s => s.name).join(', ')}
        </Typography>
        <TextField
          label=""
          fullWidth
          margin="normal"
          value={name}
          onChange={e => setName(e.target.value)}
        />
        <TextField
          label=""
          fullWidth
          margin="normal"
          value={department}
          onChange={e => setDepartment(e.target.value)}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleReserve} variant="contained" color="primary"></Button>
        <Button onClick={onClose} variant="outlined"></Button>
      </DialogActions>
    </Dialog>
  );
}
